import { useRef, useEffect } from 'react'

const GAZE_INITIAL_VALUE = { x: 0, y: 0, r: 15, isMoving: false }

const useGaze = props => {
  const { doMonitoring } = props

  const gazeRef = useRef(GAZE_INITIAL_VALUE)

  const setGazeListenerEffect = () => {
    if (!doMonitoring) {
      return
    }

    window.webgazer.clearData()
    window.webgazer
      .setGazeListener(data => {
        if (!data) {
          return
        }

        setGazePosition({ clientX: data.x, clientY: data.y })
      })
      .begin()
  }

  // eslint-disable-next-line react-hooks/exhaustive-deps
  useEffect(setGazeListenerEffect, [])

  const clearGazeListener = () => {
    return () => {
      try {
        window.webgazer.clearGazeListener()
        window.webgazer.end()
        window.webgazer.stopVideo()
      } catch {
        //
      }
    }
  }

  useEffect(clearGazeListener, [])

  const setGazePosition = e => {
    gazeRef.current.x = e.clientX
    gazeRef.current.y = e.clientY
    gazeRef.current.isMoving = true
  }

  const onGazeMove = e => {
    setGazePosition(e)
  }

  return {
    gaze: gazeRef.current,
    onGazeMove
  }
}

export default useGaze
