export const joinClasses = (...classes) => {
  return classes.filter(Boolean).join(' ')
}

export const circleRectCollision = (circle, rect) => {
  const distX = Math.abs(circle.x - rect.x - rect.w / 2)
  const distY = Math.abs(circle.y - rect.y - rect.h / 2)

  if (distX > rect.w / 2 + circle.r) {
    return false
  }

  if (distY > rect.h / 2 + circle.r) {
    return false
  }

  if (distX <= rect.w / 2) {
    return true
  }

  if (distY <= rect.h / 2) {
    return true
  }

  const dx = distX - rect.w / 2
  const dy = distY - rect.h / 2

  return dx * dx + dy * dy <= circle.r * circle.r
}

export const pickKeysFromObject = (obj, keys) => {
  return Object.fromEntries(
    keys.map(key => {
      return [key, obj[key]]
    })
  )
}

export const getRandomInt = (min, max) => {
  return Math.floor(Math.random() * (max + 1 - min) + min)
}

export const circleCircleCollision = (c1, c2) => {
  const dx = c1.x - c2.x
  const dy = c1.y - c2.y

  return Math.sqrt(dx ** 2 + dy ** 2) <= c1.r + c2.r
}

export const getAngleBetweenPoints = (p1, p2) => {
  const dx = p1.x - p2.x
  const dy = p1.y - p2.y

  return Math.atan2(dy, dx)
}
