import React, { useState, useEffect } from 'react'
import { useParams, Redirect } from 'react-router-dom'
import { useDispatch } from 'react-redux'
import { Box, CircularProgress } from '@material-ui/core'
import html2canvas from 'html2canvas'

import * as api from '../../api'

import * as routing from '../../routing'

import {
  temporarySetLayout,
  resetLayout
} from '../../redux/Layout/layout.actions'

import MonitoringRecordSidebar from './components/MonitoringRecordSidebar'
import Scenario from '../../components/Scenario'
import MonitoringCanvas from './components/MonitoringCanvas'

import { useStyles } from './MonitoringRecordPage.style'

const MonitoringRecordPage = () => {
  const dispatch = useDispatch()

  const { monitoringRecordId } = useParams()

  const classes = useStyles()

  const [monitoringRecord, setMonitoringRecord] = useState(null)
  const [contentScreenshot, setContentScreenshot] = useState(null)
  const [gazePlotLimit, setGazePlotLimit] = useState(null)
  const [isHeatmapVisible, setIsHeatmapVisible] = useState(false)

  const fetchMonitoringRecordEffect = () => {
    if (monitoringRecord) {
      return
    }

    const _monitoringRecord = api.getMonitoringRecordById(monitoringRecordId)

    if (_monitoringRecord) {
      dispatch(temporarySetLayout(_monitoringRecord.layout))
      setGazePlotLimit(_monitoringRecord.monitoringData.gazePlot.length)
    }

    setMonitoringRecord(_monitoringRecord)
  }

  useEffect(fetchMonitoringRecordEffect, [
    dispatch,
    monitoringRecord,
    monitoringRecordId
  ])

  const resetLayoutEffect = () => {
    return () => {
      dispatch(resetLayout())
    }
  }

  // eslint-disable-next-line react-hooks/exhaustive-deps
  useEffect(resetLayoutEffect, [])

  const captureContentEffect = () => {
    if (!monitoringRecord || contentScreenshot) {
      return
    }

    //!    setTimout  ,    

    setTimeout(() => {
      html2canvas(document.querySelector('#contentWidth'), {
        backgroundColor: null
      }).then(canvas => {
        setContentScreenshot(canvas)
      })
    }, 1000)
  }

  useEffect(captureContentEffect, [contentScreenshot, monitoringRecord])

  if (!monitoringRecord) {
    if (monitoringRecord === undefined) {
      return <Redirect to={routing.ROUTE_ADMIN} />
    }

    return null
  }

  const getTextPartClass = id => {
    const part = monitoringRecord.monitoringData.parts[id]

    if (!part) {
      return 'unread'
    }

    if (part.isRecognized) {
      return 'recognized'
    }

    return 'unrecognized'
  }

  const getContainerStyle = () => {
    if (contentScreenshot) {
      return {}
    }

    return {
      display: 'flex',
      flexDirection: 'column',
      justifyContent: 'center',
      alignItems: 'center'
    }
  }

  const getGazePlot = () => {
    if (gazePlotLimit === null) {
      return null
    }

    return monitoringRecord.monitoringData.gazePlot
  }

  const gazePlot = getGazePlot()

  const renderCanvas = () => {
    if (!contentScreenshot) {
      return <CircularProgress />
    }

    return (
      <MonitoringCanvas
        contentScreenshot={contentScreenshot}
        gazePlot={gazePlot}
        gazePlotLimit={gazePlotLimit}
        isHeatmapVisible={isHeatmapVisible}
      />
    )
  }

  return (
    <>
      <MonitoringRecordSidebar
        gazePlot={gazePlot}
        gazePlotLimit={gazePlotLimit}
        isHeatmapVisible={isHeatmapVisible}
        setGazePlotLimit={setGazePlotLimit}
        setIsHeatmapVisible={setIsHeatmapVisible}
      />
      <Box className={classes.canvasContainer} style={getContainerStyle()}>
        {renderCanvas()}
      </Box>
      <div style={{ width: 0 }}>
        {!contentScreenshot && (
          <Scenario
            scenario={{
              blocks: monitoringRecord.blocks
            }}
            modes={['monitoring-record']}
            cascade
            containerWidth={monitoringRecord.containerWidth}
            getTextPartClass={getTextPartClass}
          />
        )}
      </div>
    </>
  )
}

export default MonitoringRecordPage
