import React, { useState, useEffect } from 'react'
import { Stage, Layer, Image, Circle, Path } from 'react-konva'
import h337 from 'heatmap.js'

import { getAngleBetweenPoints } from '../../../../helpers'

const GAZE_PLOT_CIRCLE = {
  RADIUS: 10,
  FILL: 'rgba(156, 39, 176, 0.2)',
  STROKE: 'rgba(156, 39, 176, 0.5)'
}

const MonitoringCanvas = props => {
  const { contentScreenshot, gazePlot, gazePlotLimit, isHeatmapVisible } = props

  const [stage, setStage] = useState(null)
  const [heatmap, setHeatmap] = useState(null)

  const createHeatmapEffect = () => {
    if (!stage || heatmap) {
      return
    }

    const heatmapInstance = h337.create({ container: stage.content })

    heatmapInstance.setData({
      max: 5,
      data: gazePlot.map(gaze => {
        return { x: Math.floor(gaze.x), y: Math.floor(gaze.y), value: 1 }
      })
    })

    setHeatmap(heatmapInstance._renderer.canvas)
  }

  useEffect(createHeatmapEffect, [gazePlot, heatmap, stage])

  const { width, height } = contentScreenshot

  const renderGazePlot = () => {
    if (isHeatmapVisible) {
      return null
    }

    const _gazePlot = gazePlot.slice(0, gazePlotLimit)

    return (
      <>
        <Path
          stroke={GAZE_PLOT_CIRCLE.STROKE}
          strokeWidth={1}
          sceneFunc={(c, shape) => {
            c.beginPath()

            for (let i = 1; i < _gazePlot.length; i++) {
              const prevGaze = gazePlot[i - 1]
              const gaze = gazePlot[i]
              const angle = getAngleBetweenPoints(prevGaze, gaze)

              const p1 = {
                x: prevGaze.x - GAZE_PLOT_CIRCLE.RADIUS * Math.cos(angle),
                y: prevGaze.y - GAZE_PLOT_CIRCLE.RADIUS * Math.sin(angle)
              }
              const p2 = {
                x: gaze.x + GAZE_PLOT_CIRCLE.RADIUS * Math.cos(angle),
                y: gaze.y + GAZE_PLOT_CIRCLE.RADIUS * Math.sin(angle)
              }

              c.moveTo(p1.x, p1.y)
              c.lineTo(p2.x, p2.y)
            }

            c.closePath()
            c.strokeShape(shape)
          }}
        />
        {_gazePlot.map((gaze, gazeIndex) => {
          return (
            <Circle
              key={gazeIndex}
              x={gaze.x}
              y={gaze.y}
              radius={GAZE_PLOT_CIRCLE.RADIUS}
              fill={GAZE_PLOT_CIRCLE.FILL}
              stroke={GAZE_PLOT_CIRCLE.STROKE}
              strokeWidth={1}
            />
          )
        })}
      </>
    )
  }

  const renderHeatmap = () => {
    if (!heatmap || !isHeatmapVisible) {
      return null
    }

    return <Image image={heatmap} />
  }

  return (
    <Stage ref={setStage} width={width} height={height}>
      <Layer>
        <Image image={contentScreenshot} />
        {renderGazePlot()}
        {renderHeatmap()}
      </Layer>
    </Stage>
  )
}

export default MonitoringCanvas
