import React from 'react'
import { Typography, Switch, FormControlLabel } from '@material-ui/core'

import * as routing from '../../../../routing'

import LeftDrawer from '../../../../ui/LeftDrawer'
import Slider from '../../../../ui/Slider'

import { useStyles } from '../../../../ui/LeftDrawer/LeftDrawer.style'

const MonitoringRecordSidebar = props => {
  const {
    gazePlot,
    gazePlotLimit,
    isHeatmapVisible,
    setGazePlotLimit,
    setIsHeatmapVisible
  } = props

  const classes = useStyles()

  const renderParams = () => {
    if (!gazePlot) {
      return null
    }

    const toggleHeatmap = () => {
      setIsHeatmapVisible(!isHeatmapVisible)
    }

    return (
      <div className={classes.drawerItem}>
        <div className={classes.drawerItemParam}>
          <Typography variant="body1">  </Typography>
          <Slider
            value={gazePlotLimit}
            step={1}
            marks={[
              {
                value: 0
              },
              {
                value: gazePlot.length
              }
            ]}
            onChange={(e, value) => {
              setGazePlotLimit(value)
            }}
          />
        </div>
        <div className={classes.drawerItemParam}>
          <FormControlLabel
            control={<Switch checked={isHeatmapVisible} color="primary" />}
            label=" "
            onChange={toggleHeatmap}
          />
        </div>
      </div>
    )
  }

  return (
    <LeftDrawer
      header={{
        title: ' ',
        backRoute: routing.ROUTE_ADMIN
      }}
    >
      {renderParams()}
    </LeftDrawer>
  )
}

export default MonitoringRecordSidebar
